<template>
    <div class="manage tc">
        <button>新增</button>
        <div class="input-area">
            <input type="text" placeholder="请输入人员姓名">
            <button>确定</button>
            <button  v-on:click="capturePhoto">拍照</button>
        </div>
        <table>
            <tr>
                <th>姓名</th>
                <th>操作</th>
            </tr>
            <tr>
                <td>
                     张三
                </td>
                <td><span>编辑</span> <span>删除</span></td>


            </tr>
        </table>

        <img style="display:none;width:240px;height:320px;" id="smallImage" src="" />

        <footer-nav v-bind:class="{'isIndex':isManagePage}"></footer-nav>
    </div>
</template>

<script>
    import FooterNav from '../../components/footer.vue'

    /*
    //拍照
    function capturePhoto() {

      //拍照并获取Base64编码的图像（quality : 存储图像的质量，范围是[0,100]）
      navigator.camera.getPicture(onPhotoDataSuccess, onFail, { quality: 50,
        destinationType: destinationType.DATA_URL }
      );
    }

    //拍照成功
    function onPhotoDataSuccess(imageData) {
      console.log(imageData);
      var smallImage = document.getElementById('smallImage');
      smallImage.style.display = 'block';
      smallImage.src = "data:image/jpeg;base64," + imageData;
    }

    //拍照失败
    function onFail(message) {
      alert('拍照失败: ' + message);
    }
    */



    export default{
        components:{
            FooterNav
        },
        methods: {
          capturePhoto: function () {
              alert('进入capturePhoto方法!!!');
            //拍照并获取Base64编码的图像（quality : 存储图像的质量，范围是[0,100]）
            navigator.camera.getPicture($options.methods.onPhotoDataSuccess, this.$options.methods.onFail, {
                quality: 50,
                destinationType: destinationType.DATA_URL
              }
            );
            alert('capturePhoto方法结束!!!');
          },
          onPhotoDataSuccess: function (imageData) {
            alert('onPhotoDataSuccess!!!');
            var smallImage = document.getElementById('smallImage');
            smallImage.style.display = 'block';
            smallImage.src = "data:image/jpeg;base64," + imageData;
          },
          onFail: function (message) {
            alert('拍照失败: ' + message);
          }
        },
        data(){
          return{
            isManagePage:true
          }
        }


    }
</script>



